.profile-pic-left{
    width: 120px;
    height: 120px;
    border-radius: 50%;
}
.count-section{
    border-right: 3px solid #c3c3c3;
    padding-right: 20px;
}
.container{
    width: 76vw !important;
}
.custom-btn{
    padding: 0.6rem;
    border-radius: 4px;
  }

  .custom-btn-white{
    border:2px solid #e4e9ec;
    background-color: #fdfeff;
    color: #000;
    padding: 14px;
  }
  @media(max-width:575px){
    .container{
        width: 100vw !important;
    }
    .card{
        margin-bottom: 10px !important;
    }
  }
  .border-card{
    border: none;
    margin-top: -16px;
  }
  .profile-pic-right{
    width: 70px;
    height: 70px;
    border-radius: 50%;
}
.location-right{
    margin-top: -14px;
}
.dropdown{
    margin-left: 9rem ;
}
@media (min-width:600px)and (max-width:992px) {
    .dropdown{
        margin-left: 1rem ;
    }   
}