.login-container{
    margin-top: 5rem;
    font-family: 'Poppins', sans-serif;
  
  }
  .input-bg{
    background-color: #f3f3f3;
    color: #dbd5d5;
    font-size: 0.9rem;
    height: 2.9rem;
  }
  .custom-btn{
    padding: 0.6rem;
    border-radius: 4px;
  }
  .custom-btn-blue{
    border:2px solid #b3e0ff;
    background-color: #b3e0ff;
    color: #fff;
    padding: 14px;
  }
  .custom-btn-white{
    border:2px solid #e4e9ec;
    background-color: #fdfeff;
    color: #000;
    padding: 14px;
  }
  .socialDesktop{
    display: block;
  
  }
  .socialMobile{
    display: none;
  }
  @media only screen and (max-width:450px){
    .socialDesktop{
        display: none;
  
    }
    .socialMobile{
        display: block;
    }
    .login-container{
        margin-top: 2.5rem !important;
    }
    .card{
        border: none !important;
    }
    .shadow{
        box-shadow: none !important;
    }
    .card-body{
        padding-left: 1.4rem !important;
        padding-right: 1.4rem !important;
    }
    
  }